.navigator {
  position: fixed;
  width: 100%;
  z-index: 1; }
  .navigator-collapsed-menu {
    position: absolute;
    right: 30px;
    top: 26px;
    z-index: 53; }
    .navigator-collapsed-menu:hover path {
      stroke: #39f; }
    @media screen and (max-width: 750px) {
      .navigator-collapsed-menu {
        right: 17px; } }
  .navigator:hover .navigator-panel {
    background-color: #eee; }
  .navigator-panel {
    position: absolute;
    background-color: transparent;
    z-index: 51;
    transition: all ease 0.45s;
    border-radius: 50%;
    right: 20px;
    top: 16px;
    width: 50px;
    height: 50px; }
    .navigator-panel.show {
      background-color: #eee; }
    @media screen and (max-width: 750px) {
      .navigator-panel {
        right: 8px; } }
  .navigator-links {
    position: absolute;
    z-index: 52;
    width: 300px;
    text-align: center;
    transform: translateY(-50%);
    left: calc(50vw - 150px);
    transition: all ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 45vh; }
    .navigator-links a {
      opacity: 0;
      display: inline-block;
      margin: 15px 0;
      text-decoration: none;
      color: #222;
      transition: all ease 0.15s;
      position: relative;
      width: 100px; }
      .navigator-links a:hover {
        color: #39f;
        transform: scale(1.06); }
    .navigator-links.show a {
      animation: show-animation 0.5s ease 0.3s forwards; }
      .navigator-links.show a:nth-child(3n + 1) {
        animation-delay: 0.25s; }
      .navigator-links.show a:nth-child(3n + 2) {
        animation-delay: 0.35s; }
      .navigator-links.show a:nth-child(3n) {
        animation-delay: 0.45s; }
    .navigator-links.hidden a {
      animation: hidden-animation 0.1s ease forwards; }

@keyframes show-animation {
  0% {
    transform: translateY(-20px);
    opacity: 0; }
  100% {
    transform: translateY(0px);
    opacity: 1; } }

@keyframes hidden-animation {
  0% {
    opacity: 1;
    transform: translateY(0px); }
  100% {
    opacity: 0;
    transform: translateY(-40px); } }
