body {
  color: #121314;
  overflow-x: hidden; }

.page-common-container {
  max-width: 680px;
  padding: 0 20px;
  margin: 0 auto; }
  .page-common-container:before, .page-common-container:after {
    content: '';
    display: table; }

.page-common-logo {
  height: 100px;
  display: flex;
  align-items: center; }
  .page-common-logo img {
    height: 28px;
    position: relative;
    top: -1px; }
    .page-common-logo img:hover {
      cursor: pointer; }

.page-common-blog-title {
  font-size: 34px;
  color: #368df3;
  transition: color ease 0.15s;
  text-decoration: none;
  font-weight: 200;
  margin: 20px 0;
  display: block; }
  @media screen and (max-width: 750px) {
    .page-common-blog-title {
      font-size: 24px;
      font-weight: 500;
      margin: 15px 0; } }

.page-common-section-title {
  margin-top: 60px;
  margin-bottom: 20px;
  padding-left: 14px;
  position: relative;
  color: #666; }
  .page-common-section-title:before {
    content: '';
    background-color: #39f;
    height: 100%;
    width: 6px;
    left: 0;
    position: absolute;
    border-radius: 4px; }

a {
  color: #368df3; }

button {
  flex-shrink: 0;
  border: none;
  background-color: #39f;
  border-radius: 8px;
  padding: 11px 32px;
  display: block;
  color: #fff;
  display: inline-block; }
  button:not(:disabled):hover {
    background-color: #4af; }
  button:focus {
    outline: none; }
  button:disabled {
    opacity: 0.7;
    cursor: not-allowed; }

input,
textarea {
  width: 100%;
  border-radius: 10px;
  height: 200px;
  border: 1px solid #999;
  padding: 15px;
  box-sizing: border-box;
  display: block; }
  input:active, input:focus,
  textarea:active,
  textarea:focus {
    outline: none;
    border-color: #368df3; }

input {
  height: 40px; }

textarea {
  height: 200px; }

#nprogress {
  pointer-events: none; }

#nprogress .bar {
  background: #368df3;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px; }

#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #368df3, 0 0 5px #368df3;
  opacity: 1;
  transform: rotate(3deg) translate(0px, -4px); }
